.wrapper{width:100%;height:auto;position:relative;z-index:1;padding-top:58px;display:flex;flex-direction:row;align-items:center}@media screen and (max-width: 960px){.wrapper{background-color:#0e1014;position:fixed;top:0;left:0;display:flex;justify-content:center;padding:0;width:calc(100% - 16px - 16px);padding:16px 16px 18px;z-index:3 !important}}.wrapper .logo{display:flex;flex-direction:column;width:140px;position:relative}.wrapper .logo :global(.icon){height:24px}.wrapper .logo::after{position:absolute;top:calc(100% + 10px);content:"крупный интегратор CRM в Росcии и ещё 8 странах";font-family:"Montserrat",Arial,sans-serif;word-wrap:break-word;font-weight:100;font-size:10.1px;line-height:12px;margin-bottom:4px;color:#fff}.wrapper .navMenu{margin-left:52px;display:flex;column-gap:30.7px}.wrapper .navMenu :global(.link) span{font-weight:500;font-size:16px;line-height:19px}@media screen and (max-width: 960px){.wrapper .navMenu{column-gap:0;justify-content:space-between;width:100%;margin:0;text-transform:uppercase}.wrapper .navMenu :global(.link) span{font-size:14px}}@media screen and (max-width: 320px){.wrapper .navMenu :global(.link) span{font-size:12px}}.wrapper .links{display:flex;row-gap:5px;align-items:center;flex:1;justify-content:flex-end;flex-wrap:wrap}.wrapper .linksNested{margin-left:47px;display:flex;column-gap:30px;margin-right:-2px}.wrapper .linksNested :global(.icon){width:20px}@media screen and (min-width: 960px){.pc{display:flex}}@media screen and (max-width: 960px){.pc{display:none}}@media screen and (min-width: 960px){.mobile{display:none}}@media screen and (max-width: 960px){.mobile{display:flex}}