.heroScreen{width:100%;margin-top:127px;display:grid;grid-template-columns:repeat(12, 1fr)}@media screen and (max-width: 960px){.heroScreen{margin-top:42px;display:flex;flex-direction:column;row-gap:46px;padding:0 14px;width:calc(100% - 14px - 14px)}}.heroScreen .hero{grid-column:auto/span 4;margin-left:-2px}.heroScreen .hero h1{font-weight:400;font-size:48px;line-height:58px;margin:0 0 30px 0}.heroScreen .hero h1 b{letter-spacing:.3px;font-weight:800;background:linear-gradient(92deg, #fcb045 27.14%, #ff3f78 121.36%),#e4e5ea;-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);background-clip:text}.heroScreen .hero p{margin:0;font-family:"Montserrat";font-weight:400;font-size:18px;line-height:22px}@media screen and (max-width: 960px){.heroScreen .hero{margin-left:0}.heroScreen .hero h1{font-weight:400;font-size:36px;line-height:44px;margin:0 0 15px 0}.heroScreen .hero h1 b{letter-spacing:0px;font-weight:900;background:linear-gradient(92deg, #fcb045 27.14%, #ff3f78 121.36%),#e4e5ea;-webkit-background-clip:text;-webkit-text-fill-color:rgba(0,0,0,0);background-clip:text;margin-left:1px}}.heroScreen .advantanges{grid-column:-5/span 4}.heroScreen .advantanges h3{font-weight:500;margin:0}.heroScreen .advantanges h3 b{background:linear-gradient(90deg, #fcb045 36.99%, #fd1d1d 100%);-webkit-text-fill-color:rgba(0,0,0,0);font-weight:900;font-size:18px;text-transform:uppercase;-webkit-background-clip:text;background-clip:text;letter-spacing:.6px}.heroScreen .advantanges ul{display:grid;grid-template-columns:repeat(2, 1fr);row-gap:30px;max-width:340px;margin-bottom:60px}.heroScreen .advantanges ul li{text-align:right;justify-self:flex-end;width:96%}.heroScreen .advantanges ul li:nth-child(1),.heroScreen .advantanges ul li:nth-child(3){margin-right:-3.7%}.heroScreen .advantanges ul li h4{margin:0;font-weight:500;font-size:18px;line-height:22px;letter-spacing:.04em;text-transform:uppercase;margin-bottom:6px}.heroScreen .advantanges ul li p{font-family:"Montserrat";margin:0;font-weight:400;font-size:16px;line-height:20px}.heroScreen .advantanges .pc{margin-top:16px;grid-template-rows:repeat(2, auto);grid-template-columns:repeat(4, 1fr)}.heroScreen .advantanges .pcContent{display:flex;flex-direction:column;align-items:flex-end;grid-row:1/1;grid-column:1/span 4}.heroScreen .advantanges .pcContent h3{font-weight:500;font-size:18px;line-height:22px;text-align:right;margin-bottom:40px}.heroScreen .advantanges .pc :global(.button){grid-row:2/2;grid-column:2/5;font-weight:400;padding:20.5px 35px;margin-left:23px}@media screen and (min-width: 960px){.heroScreen .advantanges .pc{display:grid}}@media screen and (max-width: 960px){.heroScreen .advantanges .pc{display:none}}.heroScreen .advantanges .mobile{row-gap:19px;margin-left:1px}@media screen and (min-width: 960px){.heroScreen .advantanges .mobile{display:none}}@media screen and (max-width: 960px){.heroScreen .advantanges .mobile{display:grid}}.heroScreen .advantanges .mobile h3{font-weight:500;font-size:18px;letter-spacing:0px;line-height:22px}.heroScreen .advantanges .mobile h3 b{text-transform:lowercase;letter-spacing:0px;font-weight:700}.heroScreen .advantanges .mobile ul{margin:0;row-gap:14px;width:91%;margin-left:1px}.heroScreen .advantanges .mobile ul li{display:flex;align-items:center;margin:0 !important;width:100% !important}.heroScreen .advantanges .mobile ul li::before{content:"—";margin-right:5px;margin-bottom:3.5px;background:linear-gradient(90deg, #fcb045 36.99%, #fd1d1d 100%);-webkit-text-fill-color:rgba(0,0,0,0);font-weight:900;font-size:10px;-webkit-background-clip:text;background-clip:text}.heroScreen .advantanges .mobile ul li h4{font-family:"Montserrat";font-weight:400;font-size:13px;line-height:16px;margin:0;letter-spacing:.05em;text-transform:uppercase;white-space:pre}